body {
  background-image: linear-gradient(#fffffaff, #fff9b1ff);
  font-family: Cambria;
}
#logo {
  display: block;
  margin-left: auto;
  margin-right: auto;
  height: auto;
  width: 50%;
}
#img1 {
  height: auto;
  width: 100%;
}
#img2 {
  height: auto;
  width: 98%;
}
#img3 {
  height: auto;
  width: 90%;
  display: block;
  margin-left: auto;
  margin-right: auto;
}
#footer {
  height: 150px;
  background-color: #ec3237ff;
  color: #ffffff;
}
#footer div{
  display: inline-flex;
  margin-right: 10%;
  margin-left: 9%;
  padding-top: 4%;
}
#footer a{
  color: #ffffff;
  text-decoration: none;
}
.flex-container {
  display: flex;
  flex-wrap: wrap;
}
.flex-container > #left {
  width: 35%;
}
.flex-container > #right {
  width: 65%;
  text-align: auto;
}
#line {
  padding-left: 10px;
}
.center {
  text-align: center;
}
#center {
  border-bottom: #ec3237ff 2px solid;
  margin-left: 5px;
  margin-right: 5px;
}
.list {
  display: inline-block;
  border-right:  #ec3237ff 2px solid;
  padding: 5px;
  font-weight: 500;
}
#list1 {
  border: none;
}
#names {
  text-align: center;
}
@media only screen and (min-width: 320px) and (max-width: 767px) {
  .flex-container > #left {
      width: 50%;
      display: block;
      margin-left: auto;
      margin-right: auto;
      overflow: hidden;
      flex: nowrap;
  }
  .flex-container > #right {
    width: 98%;
    display: block;
    margin-left: auto;
    margin-right: auto;
    overflow: hidden;
    flex: nowrap;
  }
  #footer div{
    display: block;
    margin: none;
    text-align: center;
    padding-top: 10px;
  }
}
